<template>
    <div class="db">
        <div class="search-form">
            <el-row :gutter="10">
                <el-col :span="6">
                    <el-input placeholder="请输入过滤条件" v-model="searchKey"></el-input>
                </el-col>
            </el-row>
        </div>
        <el-table :data="filteredList" width="100%" align="right" :stripe="true"
                  header-align="right">
            <el-table-column prop="key" label="应用"></el-table-column>
            <el-table-column label="地址">
                <template slot-scope="scope">
                    <div v-for="url in scope.row.value">
                        <span class="link" @click="toPage(url)">{{url}}</span>
                    </div>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
  import service from './service'

  export default {
    name: 'list',
    data () {
      return {
        searchKey: '',
        list: []
      }
    },
    props: {
      currRow: {
        type: Object,
        required: true
      },
      visible: {
        type: Object,
        required: true
      }
    },
    methods: {
      toPage (url) {
        this.visible.page = 'iframe-page'
        this.currRow.url = url
      },
      getData () {
        return service.getData().then(({data = {}}) => {
          this.list = Object.keys(data).reduce((res, key, idx) => {
            res.push({key: key, value: data[key], index: idx + 1})
            return res
          }, [])
        })
      }
    },
    computed: {
      'filteredList' () {
        return this.list.filter(item => item.key.includes(this.searchKey)) || []
      }
    },
    mounted () {
      this.getData()
    },
    components: {}
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>


</style>



