<template>
    <div class="db-container">
        <component :is="visible.page" :currRow="currRow" :visible="visible"></component>
    </div>
</template>

<script>
  import iframe from './iframe'
  import list from './list'

  export default {
    name: 'db-container',
    data () {
      return {
        currRow: {
          url: ''
        },
        visible: {
          page: 'list'
        }
      }
    },
    components: {
      list,
      'iframe-page': iframe
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



