<template>
    <div class="iframe">
        <iframe :src="currRow.url" frameborder="0"></iframe>
    </div>
</template>

<script>
  export default {
    name: 'iframe',
    props: {
      currRow: {
        type: Object,
        required: true
      },
      visible: {
        type: Object,
        required: true
      }
    },
    computed: {
    }
  }
</script>

<style>
    .iframe {
        left: 0px;
        right: 0px;
        margin-top: -20px;
        width: 100%;
        height: 100%;
        position: absolute;
    }

    .iframe iframe {
        height: 100%;
        width: 100%;
        overflow: auto;
    }

    .el-main {
        position: relative;
    }
</style>


